import styled from 'styled-components'

export const HintText = styled.span`
  display: inline-block;
  padding: 0 1rem;
  border: 1px solid #EEE;
  border-radius: 0.125rem;
  margin-left: 1rem;
  line-height: 2.5rem;
  color: #D73B33;
  font-weight: bold;
`



